<template>
  <div id="all">
    <!--<el-tag></el-tag>-->
    <!--<el-tag type="success"></el-tag>-->
    <!--<el-tag type="info"></el-tag>-->
    <!--<el-tag type="warning"></el-tag>-->
    <!--<el-tag type="danger"></el-tag>-->
    <el-tag
      :key="tag"
      v-for="tag in dynamicTags"
      closable
      :disable-transitions="false"
      @close="handleClose(tag)">
      {{tag}}
    </el-tag>
  </div>
</template>
<script>
  export default {
    data(){
      return {
        address_detail: null, //
        userlocation: {lng: "", lat: ""},
        dynamicTags:["keyi","bcdhsj","cndjs","cndjs"]
      }
    },
    mounted(){
      this.$nextTick(function () {

        var th = this
        // Map
        var map = new BMap.Map("allmap");
        // ,
        var point = new BMap.Point(121.160724,31.173277); // 
        map.centerAndZoom(point, 15);
        map.enableScrollWheelZoom();
        var ac = new BMap.Autocomplete(    //
          {
            "input": "suggestId",
            "location": map
          })
        var myValue
        ac.addEventListener("onconfirm", function (e) {    //
          var _value = e.item.value;
          myValue = _value.province + _value.city + _value.district + _value.street + _value.business;
          this.address_detail = myValue
          setPlace();
        });

        function setPlace() {
          map.clearOverlays();    //
          function myFun() {
            th.userlocation = local.getResults().getPoi(0).point;    //
            map.centerAndZoom(th.userlocation, 18);
            map.addOverlay(new BMap.Marker(th.userlocation));    //
          }

          var local = new BMap.LocalSearch(map, { //
            onSearchComplete: myFun
          });
          local.search(myValue);

          //
          map.addEventListener("click",function(e){
            //
            console.log(th.userlocation.lng);
            //
            console.log(th.userlocation.lat);

          })
        }

      })//
    },
  }
</script>
<style scoped>
  #allmap{
    width: 400px;
    height: 400px;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "", Arial, sans-serif;
    border:1px solid green;
  }
</style>



